/* HELPER CLASSES */
.bow{
  border:1px solid white;
}
.bor{
  border:1px solid red;
}

.flerow{
  display:flex;
  flex-direction: row;
}

.flecol{
  display:flex;
  flex-direction: column;
  align-items: center;
}


/* ANIMATION CLASSES */
.highlight{
  color:#e74c3c;
  -webkit-transition: color ease-in 0.5s;
  transition: color ease-in 0.5s;
}
.slide-left{
  -webkit-animation: slide-left 0.7s ease-out forwards;
          animation: slide-left 0.7s ease-out forwards;
/*   transform: rotateY(0deg);
  transition: transform 0.7s ease-out 0.1s; */
}
.slide-right{
  -webkit-animation: slide-right 0.7s ease-out forwards;
          animation: slide-right 0.7s ease-out forwards;
}
.appear{
  -webkit-animation: just-appear 1s ease-out forwards 0.5s;
          animation: just-appear 1s ease-out forwards 0.5s;
}

@-webkit-keyframes just-appear{
  0%{
    opacity:0;
  }
  100%{
    opacity: 1;
  }
}

@keyframes just-appear{
  0%{
    opacity:0;
  }
  100%{
    opacity: 1;
  }
}

@-webkit-keyframes slide-right {
  0% {
    opacity:0;
    -webkit-transform: translateX(-30rem);
            transform: translateX(-30rem);
  }
  100%{
    opacity:1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes slide-right {
  0% {
    opacity:0;
    -webkit-transform: translateX(-30rem);
            transform: translateX(-30rem);
  }
  100%{
    opacity:1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes slide-left {
  0% {
    opacity:0;
    -webkit-transform: translateX(30rem);
            transform: translateX(30rem);
  }
  100%{
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes slide-left {
  0% {
    opacity:0;
    -webkit-transform: translateX(30rem);
            transform: translateX(30rem);
  }
  100%{
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

li{
    list-style-type: none;
}
  
a{
    text-decoration: none;
    color:white;
    font-size: 1.7rem;
    -webkit-transition: color ease-in 0.5s;
    transition: color ease-in 0.5s;
}

.navigation-main{
    background: #444;
    border-bottom: 3px solid #e74c3c;
    display:flex;
    width:100%;
    position:-webkit-sticky;
    position:sticky;
    top:0;
    z-index: 100;
}

/* navigation button on mobile */
.navigation-button{
    margin-left: 0.5rem;
    background: none;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:10px;
    border:none;
}
.navigation-button span{
    margin:3px 0;
    height:5px;
    width:30px;
    border-radius: 5px;
    background: #FEFEFE;
}

/* main navigation links */
.navigation-links{
    margin: 0.5rem 0;
    margin-left: auto;
    margin-right: 2rem;
    align-items: center;
    display: none;
}
.navigation-links ul{
    display:flex;
}
.navigation-links li{
    margin:0 1rem;
    /* border:1px solid white; */
}
.navigation-links-mobile{
    width: 100%;
    background: #444;
    position: absolute;
    display: flex;
    flex-direction: column;
    margin-top: 3rem;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.5s ease-in-out;
    transition: -webkit-transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}
.navigation-links-mobile ul li{
    margin:1rem;
}

.mobile-links-active{
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: -webkit-transform 0.5s ease-in-out;
    transition: -webkit-transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}

@media all and (min-width: 601px){
    .navigation-button{
        display:none;
    }
    .navigation-links{
        display:flex;
    }
    .navigation-links-mobile{
        display: none;
    }
}
