*{
    --factor: 1.5;
    --hex-color: #e74c3c;
}
.individual-hex{
  margin:0 0.5rem;
}

.hexa:before {
  content: "";
  position: absolute;
  background: black;
  background: var(--hex-color, black);
  width: calc(50px * 1);
  width: calc(50px * var(--factor, 1));
  height: calc(86.6025px * 1);
  height: calc(86.6025px * var(--factor, 1));
  -webkit-transform: rotate(60deg);
          transform: rotate(60deg);
}
.hexa:after {
  content: "";
  position: absolute;
  background: black;
  background: var(--hex-color, black);
  width: calc(50px * 1);
  width: calc(50px * var(--factor, 1));
  height: calc(86.6025px * 1);
  height: calc(86.6025px * var(--factor, 1));
  -webkit-transform: rotate(-60deg);
          transform: rotate(-60deg);
}

.hexa {
  position: relative;
  background: white;
  background: var(--hex-color, white);
  width: calc(50px * 1);
  width: calc(50px * var(--factor, 1));
  height: calc(86.6025px * 1);
  height: calc(86.6025px * var(--factor, 1));
  margin: 1rem 3rem;
  opacity: 0;
}

.hexa svg {
  position: absolute;
  z-index: 1;
  top: 40%;
  left: 35%;
  -webkit-transform: scale(2.5);
          transform: scale(2.5);
}

