.contact{
    padding:0;
    background: #232526;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(#414345), to(#232526));
    background: linear-gradient(to right, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

#contact > div > span{
    margin-top:2rem;
    color:white;
}

#contact > div > .sechead__under{
    border-top:5px solid white;
    width:50%;
}

.contact-message{
    color: #e74c3c;
    font-size:2rem;
    text-align: center;
    margin-bottom: 5rem;
    opacity: 0;
}

.contact-links{
    margin-bottom: 30vh;
    flex-wrap: wrap;
    justify-content: center;
    opacity: 0;
}

.contact-icon{
    display:flex;
    flex-direction: column;
    align-items: center;
    padding-top:2rem;
    width:6rem;
    height:6rem;
    margin:1rem 1rem;
    background:#222;
    border:1px solid #333;
    overflow: hidden;
    -webkit-transition: background 0.5s ease-out;
    transition: background 0.5s ease-out;
}

.contact-icon svg{
    -webkit-transform: scale(2);
            transform: scale(2);
}

.contact-icon:hover{
    padding-top:2rem;
    -webkit-animation-name: translateFromUp;
            animation-name: translateFromUp;
    -webkit-animation-duration: 0.2s;
            animation-duration: 0.2s;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-direction: forwards;
            animation-direction: forwards;
    background:#e74c3c;
    -webkit-transition: background 0.5s ease-out;
    transition: background 0.5s ease-out;
}

footer{
