    -webkit-transform: translateY(-4rem);
            transform: translateY(-4rem);
    background: #e74c3c;
}
.button-up:hover{
    color:#FFFFFF;
}
.button-up svg{
    -webkit-transform: scale(2);
            transform: scale(2);
}

@-webkit-keyframes translateFromUp {
    0% {
        opacity:0.5;
        padding-top: 0rem;
    }
    100% {
        opacity:1;
        padding-top:2rem;
    }
}

@keyframes translateFromUp {
    0% {
        opacity:0.5;
        padding-top: 0rem;
    }
    100% {
        opacity:1;
        padding-top:2rem;
    }
}



